//BLOG LIKES FUNCTIONALITY


$('.likes-count').on('click', addLike);

function addLike(event) {
    if (!$(this).hasClass('full')) {
        $(this).toggleClass('full');
        $('#likes_blog').val(parseInt($('#likes_blog').val()) + 1);        
        $('#formArticle').submit();
    }
}



