//Carousel

$(function() {
    //Code runs on wrapper jQuery
    var prevIndex=0;
    var currentIndex=1;
    var nextIndex=2;
    var lastIndex=$('.quotes').find('.quote').length-1;

    $('.quotes').on('click', '.previous', showQuote);
    $('.quotes').on('click', '.next', showQuote);
    $('.quotes-pips').on('click', '.pip', showFromPip);

    generatePips();
    setLeftClass();

    var carRunning=true;
    var interval=setInterval(function() {
        if (carRunning) {
            showNextQuote();
        }
        }, 8000);

    function showNextQuote() {
        if (currentIndex===lastIndex) {
            currentIndex = 0;
        } else {
            currentIndex++;
        }
        updateState(currentIndex);
    }

    function showQuote(event) {
        if ($(event.target).hasClass('quote')) {
            var target = $(event.target);
        } else {
            var target = $(event.target).parent();
        }
        var index= $('.quote').index(target);
        updateState(index);
    }

    function updateState(index) {
        prevIndex = index===0? lastIndex:index-1;
        currentIndex=index;
        nextIndex = index===lastIndex? 0:index+1;
        updateCarPosition();
        setLeftClass();
        updatePips();
    }

    function updateCarPosition() {
        $('.quotes').find('.previous').removeClass('previous');
        $('.quotes').find('.next').removeClass('next');
        $('.quotes').find('.current').removeClass('current');
        $('.quotes').find('.comma').removeClass('visible');

        var allQuotes = $('.quotes').find('.quote');
        var allCommas = $('.quotes').find('.comma');
        $(allQuotes[prevIndex]).addClass('previous');
        $(allQuotes[currentIndex]).addClass('current');
        $(allCommas[currentIndex]).addClass('visible');
        $(allQuotes[nextIndex]).addClass('next');
    }

    function generatePips() {
        var listContainer = $('.quotes-pips').find('ul');
        for (var i = lastIndex; i>=0; i--) {
            var newPip= $('<li class="pip"></li>');
            $(listContainer).append(newPip);
        }
        updatePips();
    }

    function updatePips() {
        $('.quotes-pips').find('.previous').removeClass('previous');
        $('.quotes-pips').find('.current').removeClass('current');
        $('.quotes-pips').find('.next').removeClass('next');
        var allPips = $('.quotes-pips').find('.pip');
        $(allPips[prevIndex]).addClass('previous');
        $(allPips[currentIndex]).addClass('current');
        $(allPips[nextIndex]).addClass('next');
    }

    function showFromPip(event) {
        var index= $('.quotes-pips li').index(event.target);
        updateState(index);
    }

    function setLeftClass() {
        var allQuotes = $('.quotes').find('.quote');
        $('.quote.left').removeClass('left');
        if (prevIndex>0) {
            var index = prevIndex - 1;
            $(allQuotes[index]).addClass('left');
        } else {
            $(allQuotes[lastIndex]).addClass('left');
        }
    }

    document.addEventListener('visibilitychange', function() {
        if (document.hidden) {
            carRunning = false;
        } else {
            carRunning = true;
        }
    });
});



